import React, { useEffect, useState } from 'react'
import axios from 'axios';
import styled from 'styled-components';
import { getPostList } from '../post/getPostList';
import { getStudyFollowingList } from '../follow/getFollowingList';
import PostItemLi from '../../pages/post/PostItemLi';
import ItemLi from '../../pages/study/ItemLi';
import { URL } from '../../lib/apis/constant/path';
import FollowRecommend from '../../pages/follow/FollowRecommend';
import swiper from '../../assets/icons/illustration/Swiper.webp'
import frame from '../../assets/icons/illustration/frame.webp'
import block from '../../assets/icons/illustration/block.webp'
import post from '../../assets/icons/illustration/Post.webp'
import study from '../../assets/icons/illustration/Study.webp'
import mentoring from '../../assets/icons/illustration/Mentoring.webp'

export default function HomeBody() {
    const [postList, setPostList]= useState([]);
    const [myFollowingList, setMyFollowingList]= useState([]);
    const [studyList, setStudyList]= useState([]);
    const userToken = localStorage.getItem('Access Token');
    const postTag= ['BEST POST üî•', 'ÏßÅÎ¨¥Î≥Ñ ÍøÄÌåÅ Í≥µÏú† üçØ'];
    const studyTag= ['Ïä§ÌÑ∞Îîî/ Î™®ÏûÑ Í∞úÏÑ§ ‚úèÔ∏è', 'Ïò®ÎùºÏù∏ ÌïôÏäµ üñ•Ô∏è'];
    const followTag= ['ÏßÄÏãùÍ≥µÏú† üí≠', 'Î©òÌÜ†ÎßÅ ÌïôÏäµ üë©üèª‚Äçüè´'];
    let num = 9;
    
    async function fetchMyPostList(){
        const list= await getPostList(num);
        setPostList([ ...list]);
    }
    async function fetchMyStudyList(){
        const list = await getStudyFollowingList(num);
        setMyFollowingList([ ...list]);
    }
    useEffect(()=>{
        fetchMyPostList();
        fetchMyStudyList();
    },[]);
    
    useEffect(() => {
        function postSort(a, b) {
        if (a.createdAt < b.createdAt) {
            return 1;
        }
        if (a.createdAt > b.createdAt) {
            return -1;
        }
        return 0;
        }

        studyFollowList()
            .then((res) => {
                setStudyList(res.flat(1).sort(postSort).slice(0,9));
        })
        .catch((error)=>{
            console.log(error);
        });
    }, [myFollowingList]);
    
        const studyFollowList = async () => {
            try {
                const studyFollowList = await Promise.all(
                    myFollowingList.map(async (list) => {
                        const res = await axios.get(
                            `${URL}/product/${list.accountname}/?limit=10`,
                            {
                                headers: {
                                    "Authorization": `Bearer ${userToken}`,
                                    "Content-type": "application/json",
                                },
                            }
                        );
                        return res.data?.product;
                    })
                );
                return studyFollowList;
            } catch (error) {
                console.log(error);
            }
        };

    return (
    <HomeDiv>
        <Home>
            <CommonSection>
                <SideFullImage>
                    <img src={post} alt="Ìè¨Ïä§Ìä∏ ÌéòÏù¥ÏßÄ ÎåÄÌëú Ïù¥ÎØ∏ÏßÄ" />
                </SideFullImage>
                <Title>EDUKET POST</Title>
                <Description>ÏßÅÎ¨¥Î≥Ñ ÏµúÏã† Ìä∏Î†åÎìúÏóê ÎßûÎäî Ï†ïÎ≥¥Î•º Í≥µÏú†Ìï¥Ïöî.</Description>
                <Tag>
                    { !postTag
                    ? []
                : postTag.map((item)=>(
                    <li>
                        <p>{item}</p>
                    </li>
                ))}
                </Tag>
                <MoreButton href='http://localhost:3000/#/post/feed' aria-label="+ Í≤åÏãúÍ∏Ä ÎçîÎ≥¥Í∏∞">+ Í≤åÏãúÍ∏Ä ÎçîÎ≥¥Í∏∞</MoreButton>
                {postList.length && 
                    <Ul>
                        {!postList 
                        ? []
                        : postList.map((item)=> (
                            <Li>
                                <PostItemLi
                                    key={item.id}
                                    {...item}
                                ></PostItemLi>
                            </Li>
                        ))}
                    </Ul>
                }
            </CommonSection>
            <CommonSection>
                <SideFullImage>
                    <img src={study} alt="Ïä§ÌÑ∞Îîî ÌéòÏù¥ÏßÄ ÎåÄÌëú Ïù¥ÎØ∏ÏßÄ" />
                </SideFullImage>
                <Title>EDUKET STUDY</Title>
                <Description>ÎÇòÏóêÍ≤å ÎßûÎäî Ïä§ÌÑ∞ÎîîÏôÄ Î™®ÏûÑÏóê Ï∞∏Ïó¨Ìï¥Ïöî!</Description>
                <Tag>
                    { !studyTag
                    ? []
                    : studyTag.map((item)=>(
                        <li>
                            <p>{item}</p>
                        </li>
                    ))}
                </Tag>
                <MoreButton href='http://localhost:3000/#/together' aria-label="+ Ïä§ÌÑ∞Îîî ÎçîÎ≥¥Í∏∞">+ Ïä§ÌÑ∞Îîî ÎçîÎ≥¥Í∏∞</MoreButton>
                {studyList.length && 
                        <Ul>
                            {!studyList 
                            ? []
                            : studyList.map((data)=> (
                                <Li>
                                    <ItemLi
                                        data={data}
                                    ></ItemLi>
                                </Li>
                            ))}
                        </Ul>
                    }
            </CommonSection>
            <CommonSection>
                <SideFullImage>
                    <img src={mentoring} alt="Î©òÌÜ†ÎßÅ ÌéòÏù¥ÏßÄ ÎåÄÌëú Ïù¥ÎØ∏ÏßÄ" />
                </SideFullImage>
                <Title>EDUKET MENTORING</Title>
                <Description> Ïû•ÏÜåÏùò Ï†úÏïΩ ÏóÜÏù¥ Îã§ÏñëÌïú Î∂ÑÏïºÏùò Î©òÌÜ†ÏôÄ Î©òÌÜ†ÎßÅ ÌïôÏäµÏùÑ Ìï† Ïàò ÏûàÏñ¥Ïöî.</Description>
                <Tag>
                    { !followTag
                    ? []
                    : followTag.map((item)=>(
                        <li>
                            <p>{item}</p>
                        </li>
                    ))}
                </Tag>
                <FollowRecommend/>
            </CommonSection>
        </Home>
    </HomeDiv>
    )
}
const HomeDiv= styled.div`
    padding: 50px 0;
    background-image: url(${block});
    background-repeat: repeat;
    background-color: #f1f2f3;
`
const Home= styled.div`
    max-width: 1400px;
    margin: 0 auto;
    padding-top: 30px;
`
const SideFullImage= styled.div`
    & img {
        width: 100%;
        padding: 20px 0 30px 0;
        border-radius: 20px;
    }
`
const CommonSection= styled.section`
    max-width: 1400px;
    margin: 40px 0;
    padding: 30px 30px;
    border-radius: 20px;
    background-color: #f1f2f3;
`
const Title= styled.p`
    margin: 0 20px;
    font-family: "Pretendard-600";
    font-size: 30px;
    font-weight: 600;
    color: #101010;
    text-align: center;
    letter-spacing: -1.5px;
`
const Description= styled.p`
    margin: 0 20px;
    font-family: "Noto_Sans_KR-400";
    font-size: 18px;
    color: #3a3a3a;
    text-align: center;
    line-height: 40px;
`
const Tag= styled.div`
    display: flex;
    margin-top: 10px;
    gap: 10px;
    justify-content: center;
    font-family: "Noto_Sans_KR-600";
    & li {
        display: inline-block;
        margin: 0 4px 4px 0;
        padding: 0 15px;
        border-radius: 10px;
        background: #f1be8b;
        box-shadow: 0 1px 4px rgba(0,0,0,0.5);
        font-size: 17px;
        color: #101010;
        line-height: 35px;
        list-style: none;
    }
`
const MoreButton= styled.a`
    margin: -33px 15px;
    font-family: "Noto_Sans_KR-400";
    font-size: 14px;
    float: right;
`
const Ul= styled.ul`
    display: flex;
    flex-wrap: wrap;
    margin: 0 0;
    padding: 0 0;
    box-sizing: border-box;
    line-height: 0;
    @media screen and (max-width: 800px) {
        flex-direction: column;
    }
`
const Li= styled.li`
    flex: 0 0 33.33333%;
    margin: 0 0 50px 0;
    padding: 0 0 0;
    vertical-align: top;
    @media screen and (max-width: 1200px) {
        flex: 0 0 33.5%;
    }
    @media screen and (max-width: 1100px) {
        flex:0 0 50%;
    }
    @media screen and (max-width: 800px) {
        width: 100%;
    }
`
